import Vue from 'vue'
//1.导⼊
import Router from 'vue-router'
//2.模块化机制 使⽤Router
Vue.use(Router)

//3.创建路由器对象
const router = new Router({
    mode: 'history', //history模式 干净的网页地址 没有#/之类的符号存在
    routes: [
        {
            path: '/',
            name: 'login',
            component: () => import('@/components/login.vue'),
            redirect: '/login'
        },
        {
            path: '/login',
            name: 'login2',
            component: () => import('@/components/login.vue'),
        },
        {
            path: '/index',
            name: 'index',
            component: () => import('@/components/index.vue'),
        },
        {
            path: '/home',
            name: 'home',
            component: () => import('@/views/Home.vue'),
        },
        // study
        {
            path: '/study',
            name: 'study',
            component: () => import('@/views/study/index.vue')
        },
        {
            path: '/blog',
            name: 'blog',
            component: () => import('@/views/study/blog.vue')
        },
        {
            path: '/table',
            name: 'table',
            component: () => import('@/views/study/table.vue')
        },
        {
            path: '/table2',
            name: 'table2',
            component: () => import('@/views/study/table2.vue')
        },
        {
            path: '/frame',
            name: 'frame',
            component: () => import('@/views/study/frame.vue')
        },
        {
            path: '/echart',
            name: 'echart',
            component: () => import('@/views/study/echart')
        },
        {
            path: '/bar',
            name: 'bar',
            component: () => import('@/views/study/echart/bar.vue')
        },
        {
            path: '/pie',
            name: 'pie',
            component: () => import('@/views/study/echart/pie.vue')
        },
        {
            path: '/piechart',
            name: 'piechart',
            component: () => import('@/components/piechart.vue')
        },
        {
            path: '/country',
            name: 'country',
            component: () => import('@/views/study/echart/map/country.vue')
        },
        {
            path: '/province',
            name: 'province',
            component: () => import('@/views/study/echart/map/province.vue')
        },
        {
            path: '/city',
            name: 'city',
            component: () => import('@/views/study/echart/map/city.vue')
        },
        {
            path: '/district',
            name: 'district',
            component: () => import('@/views/study/echart/map/district.vue')
        },
        // life
        {
            path: '/life',
            name: 'life',
            component: () => import('@/views/life/index.vue')
        },
        {
            path: '/book',
            name: 'book',
            component: () => import('@/views/life/book.vue'),
        },
        {
            path: '/anime',
            name: 'anime',
            component: () => import('@/views/life/anime.vue')
        },
        {
            path: '/movie',
            name: 'movie',
            component: () => import('@/views/life/movie.vue')
        },
        {
            path: '/music',
            name: 'music',
            component: () => import('@/views/life/music.vue')
        },
    ]
})

//挂载路由导航守卫,控制页面访问权限
router.beforeEach((to, from, next) => {
    let tokenStr = window.sessionStorage.getItem('token');
    let tokenStr2 = window.localStorage.getItem('token2');

    if (to.path === '/login') return next();
    if (to.path !== "/login" && !tokenStr && !tokenStr2) {
        next({
            path: "/login"
        })
    }

    next()
})

// 路由重复问题
const VueRouterPush = Router.prototype.push
Router.prototype.push = function push(to) {
    return VueRouterPush.call(this, to).catch(err => err)
}
export default router;
